@font-face {
  font-family: "PT Sans";
  src: url("/fonts/ptsans.woff2") format("woff2"), url("/fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("/fonts/ptsansbold.woff2") format("woff2"), url("/fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
img {
  display: block;
  max-width: 100%;
}
.content-width {
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .content-width {
    padding-left: 60px;
    padding-right: 60px;
  }
}
@media (min-width: 1200px) {
  .content-width {
    padding-left: 120px;
    padding-right: 120px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
}
.page__body {
  width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
  background-color: #ffffff;
  color: #333333;
  text-align: center;
}
@media (min-width: 768px) {
  .page__body {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page__body {
    width: 1200px;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}
.main-container__title {
  display: block;
  margin-top: 0;
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .main-container__title {
    width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
}
.main-container__map {
  margin-bottom: 10px;
}
.main-header {
  position: relative;
}
.main-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .main-header__logo {
    z-index: 2;
  }
}
.main-header__nav {
  position: relative;
}
.main-header__hero {
  display: flex;
  justify-content: center;
  margin-bottom: 33px;
  z-index: -1;
}
.logo {
  width: 180px;
  height: 96px;
}
.navigation {
  display: flex;
  justify-content: flex-end;
}
.navigation__toggle {
  width: 56px;
  height: 56px;
  border: none;
  background-color: transparent;
  background-image: url("../img/burger-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
.navigation__toggle:hover {
  opacity: 0.6;
}
.navigation__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation__toggle--cross {
  background-image: url("../img/icon-menu-close.svg");
  z-index: 2;
}
.navigation__toggle--cross:hover {
  opacity: 0.6;
}
.navigation__toggle--cross:active {
  opacity: 0.3;
}
.navigation__list {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 1;
  background-color: #81b3d2;
  color: #ffffff;
  padding-inline-start: 0;
  list-style-type: none;
  margin-block: 0;
}
.navigation__list::after {
  display: block;
  position: absolute;
  bottom: 0;
  content: "";
  z-index: 2;
  width: 100%;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .navigation__list::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    display: flex;
    justify-content: center;
    background-color: #ffffff;
    color: #000000;
  }
}
.navigation__list--open {
  display: block;
  z-index: 1;
}
.navigation__item {
  margin-bottom: 1px;
}
@media (max-width: 320px) {
  .navigation__item:hover {
    background-color: #669ec0;
  }
  .navigation__item:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);
  }
}
.navigation__item--box-shadow {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
}
@media (min-width: 768px) {
  .navigation__item--box-shadow {
    box-shadow: none;
  }
}
.navigation__item--box-shadow-mix {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .navigation__item--box-shadow-mix {
    box-shadow: none;
  }
}
.navigation__item--box-shadow-inset {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .navigation__item--box-shadow-inset {
    box-shadow: none;
  }
}
@media (min-width: 768px) {
  .navigation__item {
    margin-bottom: 0;
    width: 153px;
  }
}
@media (min-width: 1200px) {
  .navigation__item {
    width: 240px;
  }
}
@media (min-width: 768px) {
  .navigation__item:nth-child(2) {
    margin-right: 153px;
  }
}
@media (min-width: 1200px) {
  .navigation__item:nth-child(2) {
    margin-right: 240px;
  }
}
.navigation__link {
  display: block;
  color: inherit;
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  padding: 17px 0 12px 0;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 18px 0 17px 0;
    font-size: 14px;
    line-height: 21px;
  }
  .navigation__link:hover {
    color: #81b3d2;
  }
  .navigation__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
  .navigation__link--current {
    color: #766357;
  }
}
.hero {
  height: 389px;
  background-color: #81b3d2;
  background-image: url("../img/hero-cover-mobile.jpg");
  background-repeat: no-repeat;
  background-position: center bottom;
  position: relative;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("img/hero-cover-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .hero {
    height: 517px;
    background-image: url("../img/hero-cover-tablet.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("img/hero-cover-tablet@2x.jpg");
    background-size: 768px auto;
  }
}
@media (min-width: 1200px) {
  .hero {
    background-image: url("../img/hero-cover-desktop.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("img/hero-cover-desktop@2x.jpg");
    background-size: 1200px auto;
  }
}
.hero--short {
  height: 157px;
}
@media (min-width: 768px) {
  .hero--short {
    height: 289px;
  }
}
@media (min-width: 1200px) {
  .hero--short {
    height: 300px;
  }
}
.hero::before {
  content: "";
  position: absolute;
  right: 50%;
  transform: scaleX(-1);
  bottom: 0;
  background-image: url("../img/hero-decor.svg");
  background-repeat: no-repeat;
  width: 600px;
  height: 57px;
}
.hero::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  background-image: url("../img/hero-decor.svg");
  background-repeat: no-repeat;
  width: 600px;
  height: 57px;
}
.hero__slogan {
  width: 282px;
  height: 214px;
  margin-top: 90px;
}
@media (min-width: 768px) {
  .hero__slogan {
    width: 460px;
    height: 350px;
    margin-top: 78px;
  }
}
.hero__slogan--short {
  width: 229px;
  height: 39px;
  margin-top: 52px;
}
@media (min-width: 768px) {
  .hero__slogan--short {
    margin-top: 95px;
    width: 457px;
    height: 78px;
  }
}
@media (min-width: 1200px) {
  .hero__slogan--short {
    margin-top: 101px;
    width: 460px;
    height: 80px;
  }
}
.title-default {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}
.advantages__intro {
  margin-top: 0;
  margin-bottom: 32px;
}
@media (min-width: 768px) {
  .advantages__intro {
    width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
}
.advantages-card {
  background-color: #81b3d2;
  color: #ffffff;
}
@media (min-width: 768px) {
  .advantages-card {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
}
@media (min-width: 768px) {
}
.advantages-card--no-image {
  background-color: #eeeeee;
  color: #000000;
  height: 100%;
}
@media (min-width: 768px) {
  .advantages-card--no-image {
    display: block;
  }
}
.underline {
  position: relative;
}
.underline::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - 36px);
  height: 1px;
  background-color: #d6d6d6;
}
@media (min-width: 768px) {
  .underline::after {
    display: none;
  }
}
.advantage {
  padding: 40px 26px 35px;
}
.advantage__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin-block: 0;
  margin-bottom: 49px;
  color: inherit;
}
.advantage__description {
  margin: 0;
  position: relative;
}
.advantage__description::before {
  counter-increment: advantages-number;
  content: "— № " counter(advantages-number) " —";
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: -35px;
}
.advantages-list {
  counter-reset: advantages-number;
  position: relative;
  padding-inline-start: 0;
  list-style-type: none;
  margin-block: 0;
}
@media (min-width: 768px) {
  .advantages-list {
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "a a a" "b b b" "c d e";
  }
}
.advantages-list::before {
  content: "";
  position: absolute;
  top: -16px;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 384px solid transparent;
  border-right: 384px solid transparent;
  border-top: 37px solid #ffffff;
}
@media (min-width: 768px) {
  .advantages-list::before {
    display: none;
  }
}
@media (min-width: 768px) {
  .advantages-list__item--1 {
    grid-area: a;
  }
}
@media (min-width: 768px) {
  .advantages-list__item--2 {
    grid-area: b;
  }
}
@media (min-width: 768px) {
  .advantages-list__item--3 {
    grid-area: c;
  }
}
@media (min-width: 768px) {
  .advantages-list__item--4 {
    grid-area: d;
  }
}
@media (min-width: 768px) {
  .advantages-list__item--5 {
    grid-area: e;
  }
}
