}
.control {
  position: relative;
  display: inline-block;
  padding-left: 16px;
}
.control__label {
  font-weight: 700;
  text-transform: none;
}
.checkbox {
  position: relative;
}
.checkbox::before {
  position: absolute;
  left: -46px;
  content: "";
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
  opacity: 1;
}
.checkbox:hover::before {
  background-color: #e5e5e5;
}
.checkbox:active::before {
  background-color: #d5d5d5;
}
.checkbox::after {
  position: absolute;
  left: -46px;
  content: "";
  width: 46px;
  height: 46px;
  background-image: url("../img/check.svg");
  background-repeat: no-repeat;
  background-size: 22px 16px;
  background-position: center;
}
.checkbox:active::after {
  background-color: rgba(51, 51, 51, 0.3);
}
.control__input + .checkbox::after {
  content: none;
}
.control__input:checked + .checkbox::after {
  content: "";
}
.radio {
  position: relative;
}
.radio::before {
  position: absolute;
  left: -46px;
  content: "";
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 50%;
  opacity: 1;
}
.radio:hover::before {
  background-color: #e5e5e5;
}
.radio:active::before {
  background-color: #d5d5d5;
}
.radio::after {
  position: absolute;
  left: -31px;
  top: 15px;
  content: "";
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #333333;
}
.radio:active::after {
  background-color: rgba(51, 51, 51, 0.3);
  border-color: #d5d5d5;
}
.control__input + .radio::after {
  content: none;
}
.control__input:checked + .radio::after {
  content: "";
}
.button {
  padding-top: 18px;
  padding-bottom: 14px;
  border: none;
  background-color: #81b3d2;
  color: #ffffff;
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
}
.modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
}
.modal__wrapper {
  width: 100%;
}
.modal__content {
  width: 280px;
}
.modal__button-wrapper {
  padding: 20px;
  background-color: #f2f2f2;
}
.modal__button--failure {
  width: 180px;
}
